// eslint-disable-next-line
import Vue from 'vue';

import Router from 'vue-router';
// eslint-disable-next-line
import store from '../store/user';

// Containers
const DefaultContainer = () => import('@/components/base/DefaultContainer');

// Views
const Points = () => import('@/components/views/wifi/Points');
const Empty = () => import('@/components/views/Empty');
const Login = () => import('@/components/views/Login');


const routes = [
  {
    path: '/',
    redirect: '/main',
    name: 'Главная',
    component: DefaultContainer,
    children: [
      {
        path: 'main',
        name: 'Главная',
        component: Empty,
      },
      {
        path: '/wifi/points',
        name: 'Точки доступа',
        component: Points,
      },
      {
        path: '/wifi/points/history',
        name: 'История по точкам доступа',
        component: Empty,
      },
      {
        path: '/wifi/clients',
        name: 'Клиенты',
        component: Empty,
      },
      {
        path: '/wifi/clients/history',
        name: 'История по клиентам',
        component: Empty,
      },
      {
        path: '/wifi/sectors',
        name: 'Зоны',
        component: Empty,
      },
      {
        path: '/wifi/sensors',
        name: 'Датчики',
        component: Empty,
      },
      {
        path: '/wifi/statistics',
        name: 'Статистика',
        component: Empty,
      },
      {
        path: 'wifi/reports',
        name: 'Отчеты по точкам доступа и клиентам',
        component: Empty,
      },
      {
        path: '/gsm/cellular',
        name: 'Сотовые устройства',
        component: Empty,
      },
      {
        path: '/gsm/cellular/history',
        name: 'История по сотовым устройствам',
        component: Empty,
      },
      {
        path: '/gsm/sensors',
        name: 'Датчики',
        component: Empty,
      },
      {
        path: '/gsm/statistics',
        name: 'Статистика',
        component: Empty,
      },
      {
        path: '/gsm/reports',
        name: 'Отчеты',
        component: Empty,
      },
      {
        path: 'wifi/reports',
        name: 'Отчеты по сотовым устройствам',
        component: Empty,
      },
      {
        path: '/settings/user',
        name: 'Настройка пользователей',
        component: Empty,
      },
      {
        path: '/settings/system',
        name: 'Настройка системы',
        component: Empty,
      },
      {
        path: '/settings/floors',
        name: 'Настройка этажей',
        component: Empty,
      },
      {
        path: '/settings/triggers',
        name: 'Настройка тригеров',
        component: Empty,
      },
      {
        path: '/settings/sectors',
        name: 'Найстрока зон',
        component: Empty,
      },
      {
        path: '/notifications',
        name: 'События',
        component: Empty,
      },
    ],
  },
  {
    name: 'login',
    path: 'login',
    component: Login,
    meta: { isPublic: true },
  },
];

Vue.use(Router);

const router = new Router({
  mode: 'hash', // https://router.vuejs.org/api/#mode
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes,
});


const isAuthenticated = () => store.state.token;


router.beforeEach((to, from, next) => {
  if (!to.meta.isPublic && !isAuthenticated()) {
    return next({ name: 'login' });
  }

  if (to.name === 'login' && isAuthenticated()) {
    return next({ name: 'main' });
  }

  return next();
});

export default router;
