// eslint-disable-next-line
import api from '../api/login';
// eslint-disable-next-line
import router from '../router/router';

const mutations = {
  // eslint-disable-next-line
  auth: (state, { token, login }) => {
    // eslint-disable-next-line
    state.token = token;
    // eslint-disable-next-line
    state.userLogin = login;
  },

  logout: (state) => {
    // eslint-disable-next-line
    state.token = null;
    // eslint-disable-next-line
    state.userLogin = null;
  },

};

const actions = {
  auth: ({ commit }, { login, password }) => {
    api.authenticate(login, password)
      .then((res) => {
        // eslint-disable-next-line
        const token = res.token;
        if (token) {
          commit('auth', { token, login });
          router.push({ path: '/main' });
        }
      });
  },

  logout: ({ commit }) => {
    commit('logout');
    router.push({ path: '/login' });
  },


};

const getters = {
  getUser: ({ userLogin }) => userLogin,
};

const state = {
  userLogin: null,
  token: null,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
